<template>
  <div id="map" />
</template>

<script>
import gmapsInit from '@/utils/gmaps'
import gmapsStyles from '@/utils/styles.json'

export default {
  name: 'Location',

  data () {
    return {
      map: null,
      zoomOut: true,
      locations: [{
        lat: 56.955067,
        lng: 24.117528
      }],
      mapLatLng: {
        lat: 56.955067,
        lng: 24.117528
      }
    }
  },

  mounted () {
    this.createMap()
  },

  methods: {
    // Generate map with our configurations
    async createMap () {
      const google = await gmapsInit()

      this.map = new google.maps.Map(document.getElementById('map'), {
        zoom: 14,
        center: this.mapLatLng,
        disableDefaultUI: true,
        gestureHandling: 'none',
        styles: gmapsStyles
      })

      // Generate markers on the map
      let marker, icon

      this.locations.forEach(location => {
        // Set icon
        icon = {
          url: 'img/location.png', // icon location
          scaledSize: new google.maps.Size(50, 50), // scaled size
          origin: new google.maps.Point(0, 0) // origin
          // anchor: new google.maps.Point(20, 40) // anchor
        }
        // Set marker
        marker = new google.maps.Marker({
          position: new google.maps.LatLng(location.lat, location.lng),
          map: this.map,
          icon: icon
        })

        // Zoom in on click
        google.maps.event.addListener(marker, 'click', (marker, location) => {
          this.map.panTo(this.getPosition())
          switch (this.map.zoom) {
            case 14:
              this.zoomOut = true
              this.map.setZoom(10)
              break
            case 10:
              this.zoomOut ? this.map.setZoom(6) : this.map.setZoom(14)
              break
            case 6:
              this.zoomOut ? this.map.setZoom(3) : this.map.setZoom(10)
              break
            case 3:
              this.zoomOut = false
              this.map.setZoom(6)
              break
            default:
              this.map.setZoom(14)
          }
        })
      })
    }
  }
}
</script>

<style scoped>
#map {
  width: 100vw;
  height: 50vh;
}
</style>
